*
  &:before, &:after     
     box-sizing: border-box

@media (min-width: 1200px)

    body
        margin: 0px
        font-family: 'Ubuntu', sans-serif
        color:#281B19
        width: 100%
        height: 100%
        display: flex
        justify-content: center


    .head-container
        margin: 0 150px
        display: flex
        max-width: 1140px

    .container
        margin: 0 246px
        min-width: 949px
        max-width: 949px


    .main-title
        position: relative 
        font-family: 'Lato', sans-serif
        font-style: normal
        font-weight: 700
        font-size: 24px

        margin: 0px 0px 32px 0px

        color: #000000

        &::after
            position: absolute
            content: ""
            width: 40px
            height: 4px
            background-color: #63A6B9
            bottom: -8px
            left: 0        

